import React from 'react'
import {Input} from '../../../../Componentes'

export function RepresentanteLegalPJ(props){
    const onChange = (e) => {
        const target = {
            ...e.target,
            getAttribute : (attribute) => props[attribute]
        }
        if(props.onChange !== undefined) props.onChange({target})
    }

    return(
        <div>
            <div className="bg-light">
                <h2 className="my-0 text-dark">Representante Legal</h2>
            </div>

            <div className="form-row">
                <div className="form-group col-lg-12 col-md-12 col-sm-12 col-12">
                    <label htmlFor="nome">Nome Completo</label>
                    <Input className="form-control" id="nome" name="nome" type="nome-completo" uppercase="true" onChange={onChange} required minLength="7" maxLength="60"/>
                </div>

                <div className="form-group col-lg-8 col-md-8 col-sm-12 col-12">
                    <label htmlFor="cargo">Cargo</label>
                    <Input className="form-control" id="cargo" name="cargo" type="text" uppercase="true" placeholder="Proprietário, Diretor..." onChange={onChange} required minLength="2" maxLength="60" pattern="^.{2,60}$"/>
                </div>

                <div className="form-group col-lg-4 col-md-4 col-sm-12 col-12">
                    <label htmlFor="cpf">CPF</label>
                    <label className="badge badge-pill badge-info">somente números</label>
                    <label className="badge badge-pill badge-light">opcional</label>
                    <Input className="form-control" id="cpf" name="cpf" type="cpf" onChange={onChange} minLength="11" maxLength="11" pattern="^[0-9]{11}$"/>
                </div>
            </div>
        </div>
    )
}