import React, { Component } from 'react'
import {Endereco, RepresentanteLegalPJ} from '.'
import {Input} from '../../../../Componentes'
import { isFunction } from 'util';

export default class DadosDaProposta extends Component {

    constructor(props){
        super(props)

        this.state = {
            nome : '',
            cnpj : '',
            ramo_atividade : '',
            telefone : '',
            endereco : {
                cep : '',
                logradouro : '',
                bairro : '',
                cidade : '',
                uf : '',
            },
            representante_legal : {
                nome : '',
                cargo : '',
            },
        }

        this.email = React.createRef()
        this.observacoes = React.createRef()

        this.onChange = this.onChange.bind(this)
    }

    componentDidMount(){
        const {dados} = this.props

        if(dados !== undefined){   
            const {proposta} = dados

            if(proposta !== undefined){
                this.setState(prevState => ({...prevState, ...proposta}) )
            }
        }
    }

    componentWillUnmount(){
        const proposta = Object.keys(this.state)
            // Filtra os campos a depender do preenchimento (não é validação)
            .filter(chave => this.state[chave] !== '' && this.state[chave] !== undefined)
            
            //Monta o objeto aluno a partir dos campos filtrados
            .reduce((concedente, atributo) => {
                concedente[atributo] = this.state[atributo]
                return concedente
            },{})

        // Salva no requerimento os dados do aluno
        this.props.salvaDados('proposta', proposta)
    }
    
    onChange(e, rootElement = undefined) {
        const {name, value, getAttribute} = e.target
        
        if(getAttribute !== undefined && isFunction(getAttribute) & getAttribute('rootElement') !== undefined){
            rootElement = e.target.getAttribute('rootElement')
        }

        this.setState(prevState =>
            rootElement === undefined ?
                {[name]: value}
            :
                {[rootElement]: {...prevState[rootElement], [name]:value}}
        )
        
    }

    render() {
        const { 
            email,
            observacoes,
            endereco,
            representante_legal,
        } = this.state

        return (
            <div>
                <h5 className="my-1 text-dark">Dados cadastrais da Empresa/Instituição</h5>

                <div className="form-row">
                    <div className="form-group col-lg-3 col-md-3 col-sm-12 col-12">
                        <label htmlFor="cnpj">CNPJ</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <Input className="form-control" id="cnpj" name="cnpj" type="cnpj" onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-9 col-md-9 col-sm-12 col-12">
                        <label htmlFor="nome">Nome/Razão Social</label>
                        <Input className="form-control" id="nome" name="nome" type="text" uppercase="true" onChange={this.onChange} required minLength="2" maxLength="80" pattern={`^.{2,80}$`}/>
                    </div>
                </div>

                
                <div className="form-row">
                    <div className="form-group col-lg-4 col-md-4 col-sm-12 col-12">
                        <label htmlFor="ramo_atividade">Ramo de Atividade</label>
                        <Input className="form-control"
                            id="ramo_atividade"
                            name="ramo_atividade"
                            type="text"
                            placeholder="Ex.: produção de laticínios, suporte em informática..."
                            onChange={this.onChange}
                            uppercase="true"
                            required minLength="2" maxLength="60" pattern={`^.{2,60}$`}/>
                    </div>

                    <div className="form-group col-lg-3 col-md-3 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <Input className="form-control" id="telefone" name="telefone" type="telefone" onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-5 col-md-5 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" defaultValue={email!==undefined ? email : ''} ref={this.email} onChange={this.onChange} minLength="6" maxLength="70"/>
                    </div>        
                </div>

                <Endereco rootElement='endereco' onChange={this.onChange} value={endereco}/>
                <RepresentanteLegalPJ rootElement='representante_legal' onChange={this.onChange} value={representante_legal} />

                <div className="form-row">
                    <div className="form-group col-lg-12 col-md-12 col-sm-12 col-12">
                        <label htmlFor="observacoes">Observações</label>
                        <label className="badge badge-pill badge-info">quantidade máxima de caracteres: 500</label>
                        <textarea
                            className="form-control"
                            id="observacoes"
                            name="observacoes"
                            type="text"
                            placeholder="Informe aqui suas informações de contato caso esteja interessado em acompanhar esta possível parceria"
                            defaultValue={observacoes!==undefined ? observacoes : ''}
                            ref={this.observacoes}
                            onChange={(e) => this.setState({[e.target.name] : e.target.value})} maxLength="500"
                        ></textarea>
                    </div>
                </div>
            </div>
        );
    }
}