import React, { useState } from 'react'

export default function CNPJ(props){
    const [cnpj, setCnpj] = useState('')

    const comMascara = (valor) => {
        valor = semMascara(valor)
        const comprimento = valor.length

        if(comprimento > 12 && comprimento <= 14)
            return valor.replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d)/, "$1.$2.$3/$4-$5")

        else if(comprimento > 8)
            return valor.replace(/^(\d{2})(\d{3})(\d{3})(.)/, "$1.$2.$3/$4")

        else if(comprimento > 5)
            return valor.replace(/^(\d{2})(\d{3})(.)/, "$1.$2.$3")
        
        else if(comprimento > 2)
            return valor.replace(/^(\d{2})(.)/, "$1.$2")
        
        return valor
    }

    const semMascara = (valor) => valor.replace(/([^\d]{1,})/g, '')

    const onChange = (e) => {
        const {name, value} = e.target

        const novoValor = semMascara(value)
        setCnpj( novoValor )
        
        const target = { name, value : novoValor }
        if(props.onChange !== undefined) props.onChange({target})
    }

    return <input {...props} type="text" value={comMascara(cnpj)} onChange={onChange} minLength="18" maxLength="18" pattern="^[0-9]{2}.[0-9]{3}.[0-9]{3}/[0-9]{4}-[0-9]{2}$"/>
}