import React, { useState, useEffect } from 'react'

export default function Text(props) {
    const [carregado, setCarregado] = useState(false)
    const [valor, setValor] = useState('')

    const atualizaValor = (novoValor, name=undefined) => {
        setValor(novoValor)

        // se o nome não foi definido como parametro, mas foi definido nas props
        if(name === undefined && props.name !== undefined){
            // define o nome a partir das props
            name = props.name
        }

        // se algum nome foi definido e a função onChange foi passada via props
        if(name !== undefined && props.onChange !== undefined){
            // remonta a estrutura basica de um campo
            const target = { name, value: novoValor }
            
            // envia o campo para o onChange informado nas props
            props.onChange({target})
        }
    }

    const processaValor = (valor, padronizaEspacos=false) => {
        const {allowedchars, forbiddenchars, uppercase, lowercase} = props

        // Verifica se foi demandada a padronização dos espaços
        if(padronizaEspacos){
            valor = valor.trimLeft().trimRight().replace(/(\s{2,})/g, ' ')
        }

        // Se foram informados os caracteres que são permitidos
        if(allowedchars !== undefined && allowedchars !== ''){
            valor = valor.replace( new RegExp(`([^${allowedchars}])`), '')
        }

        // Se foram informados os caracteres que são proibidos
        else if(forbiddenchars !== undefined && forbiddenchars !== ''){
            valor = valor.replace( new RegExp(`([${forbiddenchars}])`), '')
        }

        // Se foi demandado que o conteúdo será padronizado com maiúsculas
        if(uppercase !== undefined && uppercase !== false){
            valor = valor.toUpperCase()
        }

        // Se foi demandado que o conteúdo será padronizado com minúsculas
        else if(lowercase !== undefined && lowercase !== false){
            valor = valor.toLowerCase()
        }

        //Retorna o valor final tratado
        return valor
    }

    useEffect(() => {
        if(props.value !== undefined && props.value !== valor){
            // similar ao componentDidMount
            if(!carregado){
                atualizaValor(processaValor(props.value, true))
                setCarregado(true)
            }

            // similar ao componentDidUpdate
            else {
                atualizaValor(processaValor(props.value))
            }
        }
    })

    const onBlur = (e) => {
        const {name} = e.target
        const value = processaValor(e.target.value, true)
        atualizaValor(value)
        
        const target = { name, value }   
        if(props.onBlur !== undefined) props.onBlur({target})
    }
    
    const onChange = (e) => {
        atualizaValor( processaValor(e.target.value) )
    }

    return <input {...props} type="text" value={valor} onBlur={onBlur} onChange={onChange}/>
}