import React from 'react'
import {Input} from '../../../../Componentes'

export function Endereco(props) {
    const { cep, uf, cidade, bairro, logradouro } = props.value

    const onChange = (e) => {
        const target = {
            ...e.target,
            getAttribute : (attribute) => props[attribute]
        }
        if(props.onChange !== undefined) props.onChange({target}, "endereco")
    }

    const carregaCEP = (cep) => {
        fetch(`https://viacep.com.br/ws/${cep}/json/`)
        .then(response => response.json())
        .then(dados => {
            const endereco = {
                cep,
                logradouro : dados.logradouro,
                bairro : dados.bairro,
                cidade : dados.localidade,
                uf : dados.uf,
            }

            Object.keys(endereco).map((name) => {
                const target = { name, value: endereco[name] }
                if(target.value !== undefined && target.value !== '') props.onChange({target}, "endereco")
                return name
            })
        })
    }

    return(
        <div className="form-row">
            <div className="form-group col-lg-2 col-sm-3 col-8">
                <label htmlFor="cep">CEP</label>
                <label className="badge badge-pill badge-info">somente números</label>
                <Input className="form-control" id="cep" name="cep" type="cep" value={cep} onBlur={()=> (cep.length===8) ? carregaCEP(cep): ''} onChange={onChange} required />
            </div>

            <div className="form-group col-lg-1 col-sm-2 col-4">
                <label htmlFor="uf">UF</label>
                <Input className="form-control" id="uf" name="uf" type="text" uppercase="true" value={uf} onChange={onChange} required minLength="2" maxLength="2" pattern="^[A-Za-z]{2}$"/>
            </div>

            <div className="form-group col-lg-6 col-sm-7 col-12">
                <label htmlFor="cidade">Cidade</label>
                <Input className="form-control" id="cidade" name="cidade" type="text" uppercase="true" value={cidade} onChange={onChange} required minLength="2" maxLength="40" pattern={`^.{2,40}$`}/>
            </div>

            <div className="form-group col-lg-3 col-sm-4 col-12">
                <label htmlFor="bairro">Bairro</label>
                <Input className="form-control" id="bairro" name="bairro" type="text" uppercase="true" value={bairro} onChange={onChange} required minLength="2" maxLength="20" pattern={`^.{2,20}$`}/>
            </div>

            <div className="form-group col-lg-12 col-sm-8 col-12">
                <label htmlFor="logradouro">Endereço</label>
                <Input className="form-control" id="logradouro" name="logradouro" type="text" uppercase="true" value={logradouro} placeholder="Rua, Avenida, Praça..." onChange={onChange} required minLength="3" maxLength="80" pattern={`^.{3,80}$`}/>
            </div>
        </div>
    )
}